// fonts

@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  src: url('./fonts/lato-v15-latin-regular.woff2') format('woff2'),
       url('./fonts/lato-v15-latin-regular.woff') format('woff'),
       url('./fonts/lato-v15-latin-regular.ttf') format('truetype');       
}
    
@font-face {
  font-family: 'Lato';
  font-style: normal;
  font-weight: 700;
  src: url('./fonts/lato-v15-latin-700.woff2') format('woff2'),
       url('./fonts/lato-v15-latin-700.woff') format('woff'),
       url('./fonts/lato-v15-latin-700.ttf') format('truetype');       
}
    
// variables
    
$dark-grey: #333333;
$light-grey: #ddd;
$white-95-pct: rgba(255, 255, 255, 0.95);
$dark-green: #238a8d;
$light-green: #73d055;
    
$lato-font: 'Lato';
$alt-sans-serif-font: sans-serif;
  
.app {
  margin: 25px 0 0 25px;
  text-align: left;
  font-family: $lato-font, $alt-sans-serif-font;
  color: $dark-grey;
}
    
.intro {
  h1 {
    margin: auto;
    font-size: 17px;
    font-weight: 700;
    line-height: 1.5em;
  }
  p {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.5em;
    max-width: 850px;
    margin-bottom: 15px;
  }
}
   
a {
  outline: none;
  text-decoration: none;
  &:link, &:visited {
    color: $dark-green;
  }
  &:active {
    color: $light-green;
  }
  &:focus, &:hover {
    color: $light-green;
    border-bottom: 1px dotted;
  }
}
  
.chart {
  text {
    font-family: $lato-font, $alt-sans-serif-font;
    font-size: 14px;
    color: $dark-grey;
  }
}
  
.barChart {
  margin: 0 auto; 
  position: relative;
}
  
.xAxis {
  path {
    stroke: none;
  }
  .tick line {
    stroke: none;
  }
  text {
    display: none;
  }
}
    
.yAxis {
  path {
    stroke: none;
  }
  .tick line {
    stroke: none;
  }
}
    
.tooltip {
  background: $white-95-pct;
  position: absolute;
  top: -12px;
  left: 0;
  padding: 0.6em 0.6em;
  border: 1px solid $light-grey;
  pointer-events: none;
  font-size: 14px;
  color: $dark-grey;
}
    
.tooltipCountry {
  margin-bottom: 0.2em;
  font-weight: 600;
}
  
.rect {
  fill: $dark-green;
  &:hover {
    fill: $light-green;
  }
}
  
.barLabel {
  fill: $dark-grey;
}  